.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
/* Reset default margin and padding for the entire page */
body,
ul,
li,
h1,
p {
  margin: 0;
  padding: 0;
}

/* Set a background color for the entire app */
body {
  background-color: gray;
  font-family: Arial, sans-serif;
}

/* Style the main container */
.component {
  max-width: 600px;
  margin: 0 auto 20px;
  padding: 20px;
  background-color: rgba(255, 255, 255, 0.493);
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
  border-radius: 5px;
}

/* Style the todo form */
form {
  display: flex;
  flex-direction: column;
  margin-bottom: 20px;
}

label {
  font-weight: bold;
  margin-bottom: 5px;
}

input[type="text"] {
  background-color: #ffffff9a;
  padding: 8px;
  font-size: medium;

  border: 1px solid #585858;
  border-radius: 3px;
  margin-bottom: 10px;
}

button[type="submit"] {
  padding: 8px 16px;
  background-color: #007bff;
  color: #ffffff;
  border: none;
  border-radius: 3px;
  cursor: pointer;
}

textarea {
  background-color: #ffffff9a;

  resize: none;
  padding: 8px;
  border: 1px solid #585858;
  border-radius: 3px;
  margin-bottom: 10px;
}
